/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

@import url('https://fonts.googleapis.com/css2?family=Hanalei&display=swap');

 * {
	margin: 0;
	padding: 0;
	border: 0;
}
/* HTML5 display-role reset for older browsers */
article, aside, details, figcaption, figure, 
footer, header, hgroup, menu, nav, section {
	display: block;
}
body {
	line-height: 1;
}
ol, ul {
	list-style: none;
}
blockquote, q {
	quotes: none;
}
blockquote:before, blockquote:after,
q:before, q:after {
	content: '';
	content: none;
}
table {
	border-collapse: collapse;
	border-spacing: 0;
}

/* General css */

.logo {
    font-family: 'Hanalei', cursive;
    font-size: calc(28px + 2.5vw);
}

.header {
    width: 100%;
    border-bottom: 4px dotted black;
}


body, html {
    background-color: #f6f6f6;
}

/* Login page css */

.login-container {
    width: 100vw;
    height: 100vh;
    display: flex;
    justify-content: center;
    align-items: center;
}

.login-box {
    padding: 70px;
    border: 1px solid black;
    border-radius: 10px;
}

.login-input {
    padding: 10px;
    border-radius: 10px;
    margin: 10px 0px;
    display: block;
    border: 0.5px solid black;
    width: 90%;
}

.login-btn {
    border-radius: 10px;
    padding: 10px;
}

.landing-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    overflow: auto;
    height: 100%;
}

.blog-card {
    width: 60%;
    display: flex;
    flex-direction: row;
    border: 1px solid #000000;
    background-color: #ffffff;
    padding: 20px;
    margin-top: 25px;
    border-radius: 10px;
    -webkit-transition-property: width;
    transition-property: width;
    -webkit-transition-duration: 0.3s;
    transition-duration: 0.3s;
    -webkit-transition-timing-function: ease-out;
    transition-timing-function: ease-out;
}

.blog-card:hover {
    width: 62%;
    box-shadow: 4px 6px 20px 0px #b8b8b8;
}

.card-title {
    flex: 1;
    margin: 0px 10px;
    font-size: calc(12px + 2vw);
    font-weight: 900;
}

/* helper css */

.d-flex {
    display: flex;
}

.m-30 {
    margin: 30px;
}

.ml-30 {
    margin-left: 30px;
}

.w-25 {
    width: 25%;
}

.w-35 {
    width: 35%;
}

.w-50 {
    width: 50%;
}

.w-100 {
    width: 100%;
}